"use client";

import AdminAvatar from "@/app/components/adminAvatar";
import React from "react";
import CollapsibleMenu from "./CollapsibleMenu";

export default function Sidebar() {
  return (
    <div className="bg-gray-900 text-white max-w-full min-h-screen flex">
      <div className="bg-gray-800 w-64 h-screen p-4 flex flex-col">
        <AdminAvatar />

        <div className="flex-1">
          <button className="w-full text-left mb-4">+ Новый документ</button>

          <CollapsibleMenu
            title="Документы"
            items={[
              { name: "Входящие", count: 3 },
              { name: "Исходящие", count: 4 },
              { name: "Подписанные", count: 5 },
              { name: "Корзина", count: 0 },
            ]}
          />

          <CollapsibleMenu
            title="Организация"
            items={[{ name: "Сотрудники" }]}
          />

          <CollapsibleMenu
            title="Справочники"
            items={[
              { name: "Виды орган-й" },
              { name: "Виды док-в" },
              { name: "Маршруты" },
            ]}
          />

          <button className="w-full text-left">Администрирование</button>
        </div>
      </div>

      <div className="flex-1 flex items-center justify-center">
        <div className="text-center">
          <h1 className="text-4xl mb-4">DDM</h1>
          <p className="text-lg text-gray-400">Digital document management</p>
        </div>
      </div>
    </div>
  );
}
